.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal-content {
    background-color: #fff;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    width: 90%;
    max-width: 500px;
    text-align: left;
}

.modal-content h3 {
    margin-top: 0;
    color: #333;
}

.modal-content p {
    margin: 0.5rem 0;
    color: #555;
}

.modal-content .btn {
    margin-top: 1rem;
    width: 100%;
}